@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  text-align: center;
  margin: 20px auto;
  max-width: 500px;
}

body {
  background-image: linear-gradient(#ffffff, #feef65);
  color: hwb(123 20% 18%);
  font-family: "Roboto", sans-serif;
  max-width: 500px;
  padding: 0;
}

h1 {
  font-size: 6.4em;
  font-weight: 800;
  margin: 10px auto;
  padding: 0;
}

h2 {
  font-size: 2.6em;
  margin: 10px auto;
  padding: 0;
}

a {
  color: #30b83d;
}

#form {
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: none;
}

input[type="text"] {
  font-size: 1.2em;
  color: #30b83d;
  border-color: #30b83d;
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  width: 80%;
}

/* Set placeholder text color to black */
::placeholder {
  color: #30b83d;
}

#submit {
  background-color: #30b83d;
  color: #e6ebf0;
  font-size: 1.2em;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 30px;
}

#submit:hover {
  background-color: #30b83d;
}

p {
  font-size: 1.2em;
  word-wrap: break-word;
  text-align: left;
  padding: 10px;
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}
